   @import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,300&family=Fjalla+One&family=Inter:wght@300&family=Outfit:wght@300&family=Poppins:wght@400;500&family=Roboto&display=swap');
 
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
 
.flex-container {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  width: 100%;
  flex-wrap: wrap;
  margin-bottom: 12px;
}
.flex-container-1 {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  width:100%;
  flex-wrap: wrap;
margin-top: 17px;
}

img {
  margin-right: auto;
  margin-left: 7px;
  height: 131px;
}
 
.flex-item {
  box-shadow: 1px 3px 9px darkgreen;
  background: transparent;
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  flex-basis: 100px;
}

 
.flex-items {
  box-shadow: 1px 3px 9px #555;
  background: transparent;
  display: flex;
  height:60px;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  flex-basis: 100px;
}
 
h2.description {
  margin-right: 59px;
}


.container{
  background: url( climate.jpeg);
  background-attachment: fixed;
  height: 600px;  
  background-position: center;  
  background-repeat: no-repeat; 
  background-size: cover;
  color:white;

}

.flex-item-1{
  background: #c6c0cf;
  color:black;
  /* background-color: #b3e0f2; */
  display: flex;
  height:60px;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  flex-basis: 100px;
  box-shadow: 1px 3px 11px #555;

}

input.input-data {
  width: 225px;
  margin-top: 16px;
  padding: 5px 6px 7px 14px;
  color: black;
}

.city {
  margin-top: 16px;
}

.data-container {
  margin-top: 23px;
}

@media (max-width: 768px) {
  .container{
    background: url(sun.jpeg); 
    background-attachment: fixed;
    height: 600px;  
    background-position: center;  
    background-repeat: no-repeat; 
    background-size: cover;
    color:whitesmoke;
  
  }
  img {
    margin-right: auto;
    margin-left: 7px;
    height: 115px;
}
input.input-data {
  width: 240px;
  margin-top: 16px;
  padding: 5px 6px 7px 19px;
  color: black;
  margin: 16px 33px 12px 46px;
}
}